	module.exports = {"data":{"markdownRemark":{"html":"<h1>HTML Entities</h1>\n<h2>Overview</h2>\n<p>•\tWhat are HTML Entities?</p>\n<p>HTML entities are characters that are used to replace reserved characters in HTML or for characters that do not appear on your keyboard. Some characters are reserved in HTML. If you use the less than(&#x3C;) or greater than(>) signs in your text, the browser might mix them up with tags.</p>\n<p>•\tWhat are they used for?</p>\n<p>As mentioned about HTML entities are used in order to replace reserved characters that are reserved by HTML.</p>\n<p>• How do you use them?</p>\n<p>A character entity looks similar to this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code>&amp;entity_name;\n</code></pre>\n      </div>\n<p>Or</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code>&amp;#entitiy_number;\n</code></pre>\n      </div>\n<p>To display the less than sign(&#x3C;) we must write it like so: &#x3C; or &#x3C;</p>\n<h3>Reference Guide</h3>\n<ul>\n<li><a href=\"https://dev.w3.org/html5/html-author/charref\">https://dev.w3.org/html5/html-author/charref</a></li>\n<li>By no means is this an exhaustive list but the above link will be able to give you more entities if the ones below do not work for your needs. Happy Coding :bowtie: </li>\n</ul>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code>Character\tEntity Name\tEntity Number\tDescription\n|       |       | <span class=\"token entity\" title=\"&#32;\">&amp;#32;</span> | Space |\n|-------|-------|-------|-------|\n|   !   |       | <span class=\"token entity\" title=\"&#33;\">&amp;#33;</span> | Exclamation mark |\n|-------|-------|-------|------------------|\n|   \"   |\t      | <span class=\"token entity\" title=\"&#34;\">&amp;#34;</span> | Quotation mark |\n|-------|-------|-------|----------------|\n|   #   |\t      | <span class=\"token entity\" title=\"&#35;\">&amp;#35;</span> | Number sign |\n|-------|-------|-------|-------------|\n|   $   |       | <span class=\"token entity\" title=\"&#36;\">&amp;#36;</span> | Dollar sign |\n|-------|-------|-------|-------------|\n|   %   |\t      | <span class=\"token entity\" title=\"&#37;\">&amp;#37;</span> | Percent sign |\n|-------|-------|-------|--------------|\n|   &amp;   |\t<span class=\"token entity\" title=\"&amp;\">&amp;amp;</span>\t| <span class=\"token entity\" title=\"&#38;\">&amp;#38;</span> | Ampersand |\n|-------|-------|-------|-----------|\n|   '   |\t\t    | <span class=\"token entity\" title=\"&#39;\">&amp;#39;</span>\t| Apostrophe |\n|-------|-------|-------|------------|\n|   (   |\t\t    | <span class=\"token entity\" title=\"&#40;\">&amp;#40;</span> | Opening/Left Parenthesis |\n|-------|-------|-------|--------------------------|\n|    )  |\t\t    | <span class=\"token entity\" title=\"&#41;\">&amp;#41;</span> | Closing/Right Parenthesis |\n|-------|-------|-------|---------------------------|\n|   *   |\t\t    | <span class=\"token entity\" title=\"&#42;\">&amp;#42;</span> | Asterisk |\n|-------|-------|-------|----------|\n|   +   |\t\t    | <span class=\"token entity\" title=\"&#43;\">&amp;#43;</span> | Plus sign|\n|-------|-------|-------|----------|\n|   ,   | \t\t  | <span class=\"token entity\" title=\"&#44;\">&amp;#44;</span>\t| Comma |\n|-------|-------|-------|-------|\n|   -   |\t\t    | <span class=\"token entity\" title=\"&#45;\">&amp;#45;</span>\t| Hyphen |\n|-------|-------|-------|--------|\n|   .   |\t\t    | <span class=\"token entity\" title=\"&#46;\">&amp;#46;</span>\t| Period |\n|-------|-------|-------|--------|\n|   /   | \t    | <span class=\"token entity\" title=\"&#47;\">&amp;#47;</span>\t| Slash |\n|-------|-------|-------|-------|\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<ul>\n<li>There are plenty of HTML entites references out there. One example is <a href=\"https://dev.w3.org/html5/html-author/charref\">https://dev.w3.org/html5/html-author/charref</a></li>\n<li>The other one is at <a href=\"https://www.w3schools.com/html/html_entities.asp\" target=\"_blank\" rel=\"nofollow\">W3Schools HTML Entities</a></li>\n</ul>","fields":{"slug":"/html/html-entities/"},"frontmatter":{"title":"HTML Entities"}}},"pathContext":{"slug":"/html/html-entities/"}}

